<template>
        <div class="login-container">
            <el-card header="欢迎访问后台管理" class="login-card">
                <el-tabs type="border-card">
                <el-tab-pane label="登录">
                    <el-form
                    @submit.native.prevent="login"
                    >
                    <el-form-item label="用户名">
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码">
                        <el-input v-model="form.password" show-password></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" native-type="submit">登录</el-button>
                    </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="注册">
                    <el-form 
                    :model="register"
                    ref="register-form"
                    :rules="rules"
                    >
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="register.username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input v-model="register.password"></el-input>
                    </el-form-item>
                    <el-form-item prop="power">
                        <el-radio v-model="register.power" label="0" border>游客</el-radio>
                        <el-radio v-model="register.power" label="999" border>超级管理员</el-radio>
                    </el-form-item>
                   
                    <el-form-item>
                        <el-button type="warning" @click="createUser">注册</el-button>
                    </el-form-item>
                    
                    </el-form>
                </el-tab-pane>
                
                </el-tabs>
            </el-card>
        </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                username:'',
                password:''
            },
            register:{
                username:'',
                password:'',
                power: '',
            },
            rules:{
                username:[
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 5, max: 15, message: '长度为5-15位', trigger: 'blur' }
                ],
                password:[
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 5, max: 15, message: '长度至少5-15位', trigger: 'blur' }
                ],
                power: [
                    { required: true, message: '请选择一个', trigger: 'change' }
                ],
            }
        }
    },
    methods:{
        createUser() {
        this.$refs['register-form'].validate((valid) => {
          if (!valid) {
            return 
          } else {
            this.cc()
          }
        });
      },
        async cc(){
            const res = await this.$http.post('/register',this.register)
            if(res.status === 409){
                return alert('123')
            }else{
                this.$message({
                message:'注册成功,可以去登录了',
                type:'success'
            })
            }
            this.register.username = ''
            this.register.password = ''
        },
        async login(){
            const res = await this.$http.post('/login',this.form)
            
            console.log(res);
            window.localStorage.setItem('user',JSON.stringify(res.data.token))
            window.localStorage.setItem('power',JSON.stringify(res.data.user.power).split(' '))
                this.$router.push('/commodity-list')
                this.$message({
                    message:'登录成功',
                    type:'success'
                })
            
        }
    }
}
</script>
<style lang="less" scoped>
.login-container{
  width: 30rem;
  margin:5rem auto;
}
html{
    height: 100vh;
}
</style>